.loading-container{
  @apply absolute flex w-full h-full items-center justify-center text-cinza-100 flex-col text-xl z-[1] bg-cinza-500
}

.loading-container span{
  @apply m-2
}

.loader{
  @apply border-8 border-solid border-cinza-350 border-t-verde-100 w-20 h-20 animate-spin rounded-full
}
