.search-containers {
  @apply bg-cinza-400 w-[50%] min-w-[50%] pl-3 pr-3 rounded-xl shadow-md mb-4
}

/*.Cursos-info {
  @apply flex-col flex w-2/4
}

.list-cursos {
  @apply flex justify-between items-center bg-cinza-400 border border-cinza-400 shadow-md transition-all text-white w-full mb-3 pr-3
}

.list-cursos:hover .title-curso {
  @apply text-[20px] text-verde-100 transition-all duration-500
}

.list-cursos:hover {
  @apply shadow-verde-100 shadow-md transition-all duration-300
}

.acessar-button {
  @apply bg-cinza-500 text-white font-semibold px-2 rounded-xl shadow-md m-1 h-[60px] w-[60px] flex justify-center
}

.play-button {
  @apply items-center m-[auto] p-[auto]
}

.msg-button {
  @apply hidden
}

.acessar-button:hover .msg-button {
  @apply flex text-[11px]
}

.desc-curso {
  @apply text-xs w-[-100%] h-[-100%] text-[12px]
}

.list-cursos:hover .desc-curso {
  @apply text-xs text-[15px] text-green-200 flex duration-700 shadow-verde-100
}

/**/
.CardContainer{
  @apply w-72 h-44 shadow-lg transition-all flex flex-col
} 

/* ANIMATIONS */
.CardContainer:hover .NomeTreinamento{
  @apply translate-y-0
}
.CardContainer:hover .NomeTreinamentoComplete{
  @apply translate-y-0
}
.CardContainer:hover .PrazoList{
  @apply translate-y-0
}
.CardContainer:hover .PrazoListConclused{
  @apply translate-y-0
}
.CardContainer:hover .DescTreinamento{
  @apply text-cinza-200 flex
}
/* ANIMATIONS */


.Card{
  @apply w-full h-44 bg-cinza-400 rounded
}
/* INFO TREINAMENTO */
.DescTreinamento{
  @apply text-cinza-200 transition-all duration-700 translate-y-0 hidden
}
.NomeTreinamento{
  @apply text-xl translate-y-5 duration-500
}
.NomeTreinamentoComplete{
  @apply text-xl translate-y-5 duration-500
}

/* BUTTON */
.BtnCardTreinamento{
  @apply flex items-center h-7 rounded-2xl gap-2 pr-3 duration-500 transition-all
}
.BtnCardTreinamento span{
  @apply h-full text-cinza-500 rounded-full flex items-center p-1 duration-500
}
.BtnCardTreinamento:hover{
  @apply pr-4
}
.BtnCardTreinamento:hover span{
  @apply rotate-180
}

